import React from "react"
import { Button } from "reactstrap"
import { MyLoading } from "src/core/components"
import styled from "styled-components"
import apiWithConfigApi from "src/services/apiConfigApi"
import useConfigTableCell from "../../hooks/useConfigTableCell"
import {
  FlowCheckConfirmMsg,
  FlowGetTextDisplay,
  FlowParseResponseAndNeedReload,
} from "src/helpers/Flows/TableCell"
import { fnListMyTableCell } from "src/helpers/fnList"

const WrapCell = styled.div`
  padding: 2px;
  & button {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    word-break: break-all;
    width: 100%;
    // border: 0px;
    height: 100%;
  }
`
export default function MTCApi(props) {
  const ctc = useConfigTableCell({ props })
  // console.log("render MTCApi:", props, ctc)
  if (fnListMyTableCell.fnGetIsHide(ctc.fnList)) {
    return ctc.renderHide()
  }
  return (
    <WrapCell className={ctc.classNameWrap("mtc-api")} style={ctc.styleWrap()}>
      <Button
        title={props.cell}
        color="primary"
        outline
        onClick={() => {
          FlowCheckConfirmMsg({
            ...ctc,
            cb: () => {
              const configApi = ctc.oneTableCell.getConfigApi()
              console.warn("click api", configApi, ctc.oneTableCell.fnList)
              if (configApi) {
                ctc.setIsFetching(true)
                apiWithConfigApi(configApi)
                  .then(res => {
                    //test fullpathlist
                    // res.Data.FullPath = null
                    // res.Data.FullPathList = [
                    //   "http://google.com",
                    //   "http://facebook.com",
                    // ]
                    //

                    //test change value empty to hide btn
                    // res.Data.btn_ExportConfigJson = ""

                    console.warn("onClickBtn API rs", res)
                    ctc.setIsFetching(false)
                    FlowParseResponseAndNeedReload({
                      ...ctc,
                      oneTableCell: ctc.oneTableCell,
                      response: res,
                    })
                  })
                  .catch(e => {
                    ctc.setIsFetching(false)
                  })
              } else {
                console.warn("Missing configApi")
              }
            },
          })
        }}
        {...ctc.getMorePropsButton()}
      >
        {ctc.isFetching ? (
          <MyLoading type="circle" />
        ) : (
          FlowGetTextDisplay({ ...ctc, isButton: true })
        )}
      </Button>
    </WrapCell>
  )
}
